import { Component, OnInit } from '@angular/core';
import { liucun } from 'src/assets/data/liucun';

@Component({
  selector: 'spark-liucun',
  templateUrl: './liucun.component.html',
  styleUrls: ['./liucun.component.less']
})
export class LiucunComponent implements OnInit {

  cols = [{ header: '日期', field: 'firstTime' }];
  datas;

  constructor() {
    for (let i = 0; i < 32; i++) {
      this.cols.push({ header: '第' + (i + 1) + '天', field: 'day_' + i });
    }
  }

  ngOnInit() {
    this.datas = liucun
  }

}
